:global .scale-transition-enter,
:global .scale-transition-exit {
  position: relative; }

:global .scale-transition-enter-active {
  z-index: 2; }
  :global .scale-transition-enter-active :local {
    -webkit-animation: moveFromRight 1000ms ease both;
            animation: moveFromRight 1000ms ease both; }

:global .scale-transition-exit-active {
  z-index: 1; }
  :global .scale-transition-exit-active :local {
    -webkit-animation: scaleDown 1000ms ease both;
            animation: scaleDown 1000ms ease both; }

@-webkit-keyframes moveFromRight {
  from {
    -webkit-transform: translateX(100%);
            transform: translateX(100%); } }

@keyframes moveFromRight {
  from {
    -webkit-transform: translateX(100%);
            transform: translateX(100%); } }

@-webkit-keyframes scaleDown {
  to {
    opacity: 0;
    -webkit-transform: scale(0.8);
            transform: scale(0.8); } }

@keyframes scaleDown {
  to {
    opacity: 0;
    -webkit-transform: scale(0.8);
            transform: scale(0.8); } }



