:global .slide-transition-exit-active :local {
  -webkit-animation: slideOut 1000ms both ease;
          animation: slideOut 1000ms both ease; }

:global .slide-transition-enter-active :local {
  -webkit-animation: slideIn 1000ms both ease;
          animation: slideIn 1000ms both ease; }

@-webkit-keyframes slideOut {
  0% { }
  25% {
    opacity: 0.5;
    -webkit-transform: translateZ(-500px);
            transform: translateZ(-500px); }
  75%,
  100% {
    opacity: 0.5;
    -webkit-transform: translateZ(-500px) translateX(-200%);
            transform: translateZ(-500px) translateX(-200%); } }

@keyframes slideOut {
  0% { }
  25% {
    opacity: 0.5;
    -webkit-transform: translateZ(-500px);
            transform: translateZ(-500px); }
  75%,
  100% {
    opacity: 0.5;
    -webkit-transform: translateZ(-500px) translateX(-200%);
            transform: translateZ(-500px) translateX(-200%); } }

@-webkit-keyframes slideIn {
  0%,
  25% {
    opacity: 0.5;
    -webkit-transform: translateZ(-500px) translateX(200%);
            transform: translateZ(-500px) translateX(200%); }
  75% {
    opacity: 0.5;
    -webkit-transform: translateZ(-500px);
            transform: translateZ(-500px); }
  100% {
    opacity: 1;
    -webkit-transform: translateZ(0) translateX(0);
            transform: translateZ(0) translateX(0); } }

@keyframes slideIn {
  0%,
  25% {
    opacity: 0.5;
    -webkit-transform: translateZ(-500px) translateX(200%);
            transform: translateZ(-500px) translateX(200%); }
  75% {
    opacity: 0.5;
    -webkit-transform: translateZ(-500px);
            transform: translateZ(-500px); }
  100% {
    opacity: 1;
    -webkit-transform: translateZ(0) translateX(0);
            transform: translateZ(0) translateX(0); } }



