.wrapper {
  max-width: 400px;
  margin: auto;
  text-align: center; }
  @media (min-width: 900px) {
    .wrapper {
      max-width: 700px; } }
  .wrapper .heading {
    margin: 0 var(--padding-large-2); }
    .wrapper .heading h3 {
      font-family: 'Lato', sans-serif;
      font-size: 1.15em;
      line-height: 1.7em;
      color: #8a8a8a;
      word-spacing: 0.02em; }
  .wrapper .projects-grid {
    display: grid;
    grid-template-columns: 1fr;
    justify-items: center;
    width: 100%;
    list-style: none; }
    @media (min-width: 900px) {
      .wrapper .projects-grid {
        grid-template-columns: repeat(2, 1fr); } }
    .wrapper .projects-grid .grid-item {
      display: block;
      padding: var(--padding-large);
      width: 100%; }



