.text section {
  margin-bottom: 2em; }
  .text section h2 {
    display: block;
    position: relative;
    padding: 0; }
  .text section ul {
    list-style: circle;
    padding: 0 1.4em;
    margin-bottom: 0.6em; }
    .text section ul li {
      font-size: 1.1em;
      line-height: 1.45em;
      color: #8a8a8a; }

.text hr {
  display: block;
  height: 1px;
  width: 50%;
  background-color: #484848;
  position: relative;
  border: 0;
  margin: 0.5em 0 1em; }



