/* Global Defaults */
html,
body {
  margin: 0px;
  padding: 0px;
  height: 100%; }

html {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

*,
*:before,
*:after {
  -webkit-box-sizing: inherit;
          box-sizing: inherit; }

body {
  font: 1em/1.25 'Open Sans', sans-serif; }

/* Headlines */
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
  font-family: MontSerrat, Tahoma, Arial, sans-serif;
  font-weight: 700;
  font-size: inherit;
  color: var(--primary-text-color);
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none; }

h1 {
  font-size: 2.225em;
  line-height: 1.45em;
  letter-spacing: -1px;
  text-shadow: 2px 2px 0 rgba(0, 0, 0, 0.15);
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  margin-bottom: 0.3em; }
  @media (min-width: 900px) {
    h1 {
      font-size: 2.625em; } }

h2 {
  font-size: 1.4em;
  line-height: 1.35em;
  letter-spacing: -0.045em; }
  @media (min-width: 900px) {
    h2 {
      font-size: 1.4em;
      line-height: 1.35em;
      letter-spacing: -0.045em; } }

/* Text Styles */
p,
th,
td,
li,
dd,
dt,
ul,
ol,
blockquote,
q,
acronym,
abbr,
a,
input,
select,
textarea {
  margin: 0;
  padding: 0;
  font-family: 'Lato', Helvetica, Arial, sans-serif; }

p {
  color: var(--secondary-text-color);
  font-size: 1.1em;
  line-height: 1.45em;
  margin-bottom: 0.7em; }
  @media (min-width: 900px) {
    p {
      font-size: 1.2em; } }

blockquote {
  margin: 1.25em;
  padding: 1.25em; }

q {
  font-style: italic; }

acronym,
abbr {
  cursor: help;
  border-bottom: 1px dashed; }

small {
  font-size: 0.85em; }

big {
  font-size: 1.2em; }

/* Links and Images */
a,
a:link,
a:visited,
a:active,
a:hover {
  text-decoration: none; }

img {
  border: none; }

/* Tables */
table {
  margin: 0;
  padding: 0;
  border: none; }

/* Forms */
form {
  margin: 0;
  padding: 0; }

label {
  cursor: pointer; }



